body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.nav-pills {
    position: fixed;
    top:0;
    left: 0;
    background: #5133a4;
    min-height: 65px;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: 1.5em;
    font-weight: bold;
    z-index: 99;
    white-space: nowrap;
}

.nav-pills a {
    color: #fff;
}

.nav-pills a:hover {
    color: #fff;
    background-color: #5133a4 !important;
}

.desc-normal {
    color: #fff;
}

.desc-correct {
    color: #0f0;
}

.desc-incorrect {
    color: #f00;
}

li {
    padding: 1% 13% 0% 5%;
    text-align: center;
}
.header {
    background-color: rgba(40, 245, 67, 0.959);
    color: blue;
    max-height: 300px;
    padding: 130px 20px;
    padding-bottom: 140px;
    text-align: center;
}



.card {
    -webkit-align-items: center;
            align-items: center;
    max-width: 250px;
    height: 200px auto;
    margin: 15px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }
  

.footer{
	background-color: #5133a4;
	width: 100%;
    position: relative;
	height: 60px;
 box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}

.footer p{
	color: white;
	text-align: center;
	padding: 20px 0;
	font-size: 15px;
}

